ne !important;\n}\n","// stylelint-disable declaration-no-important\n\n// Common values\n\n// Sass list not in variables since it's not intended for customization.\n// stylelint-disable-next-line scss/dollar-variable-default\n$positions: static, relative, absolute, fixed, sticky;\n\n@each $position in $positions {\n  .position-#{$position} { position: $position !important; }\n}\n\n// Shorthand\n\n.fixed-top {\n  position: fixed;\n  top: 0;\n  right: 0;\n  left: 0;\n  z-index: $zindex-fixed;\n}\n\n.fixed-bottom {\n  position: fixed;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-fixed;\n}\n\n.sticky-top {\n  @supports (position: sticky) {\n    position: sticky;\n    top: 0;\n    z-index: $zindex-sticky;\n  }\n}\n","//\n// Screenreaders\n//\n\n.sr-only {\n  @include sr-only();\n}\n\n.sr-only-focusable {\n  @include sr-only-focusable();\n}\n","// Only display content to screen readers\n//\n// See: https://a11yproject.com/posts/how-to-hide-content/\n// See: https://hugogiraudel.com/2016/10/13/css-hide-and-seek/\n\n@mixin sr-only {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  padding: 0;\n  overflow: hidden;\n  clip: rect(0, 0, 0, 0);\n  white-space: nowrap;\n  border: 0;\n}\n\n// Use in conjunction with .sr-only to only display content when it's focused.\n//\n// Useful for \"Skip to main content\" links; see https://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1\n//\n// Credit: HTML5 Boilerplate\n\n@mixin sr-only-focusable {\n  &:active,\n  &:focus {\n    position: static;\n    width: auto;\n    height: auto;\n    overflow: visible;\n    clip: auto;\n    white-space: normal;\n  }\n}\n","// stylelint-disable declaration-no-important\n\n.shadow-sm { box-shadow: $box-shadow-sm !important; }\n.shadow { box-shadow: $box-shadow !important; }\n.shadow-lg { box-shadow: $box-shadow-lg !important; }\n.shadow-none { box-shadow: none !important; }\n","// stylelint-disable declaration-no-important\n\n// Width and height\n\n@each $prop, $abbrev in (width: w, height: h) {\n  @each $size, $length in $sizes {\n    .#{$abbrev}-#{$size} { #{$prop}: $length !important; }\n  }\n}\n\n.mw-100 { max-width: 100% !important; }\n.mh-100 { max-height: 100% !important; }\n","// stylelint-disable declaration-no-important\n\n// Margin and Padding\n\n@each $breakpoint in map-keys($grid-breakpoints) {\n  @include media-breakpoint-up($breakpoint) {\n    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);\n\n    @each $prop, $abbrev in (margin: m, padding: p) {\n      @each $size, $length in $spacers {\n\n        .#{$abbrev}#{$infix}-#{$size} { #{$prop}: $length !important; }\n        .#{$abbrev}t#{$infix}-#{$size},\n        .#{$abbrev}y#{$infix}-#{$size} {\n          #{$prop}-top: $length !important;\n        }\n        .#{$abbrev}r#{$infix}-#{$size},\n        .#{$abbrev}x#{$infix}-#{$size} {\n          #{$prop}-right: $length !important;\n        }\n        .#{$abbrev}b#{$infix}-#{$size},\n        .#{$abbrev}y#{$infix}-#{$size} {\n          #{$prop}-bottom: $length !important;\n        }\n        .#{$abbrev}l#{$infix}-#{$size},\n        .#{$abbrev}x#{$infix}-#{$size} {\n          #{$prop}-left: $length !important;\n        }\n      }\n    }\n\n    // Some special margin utils\n    .m#{$infix}-auto { margin: auto !important; }\n    .mt#{$infix}-auto,\n    .my#{$infix}-auto {\n      margin-top: auto !important;\n    }\n    .mr#{$infix}-auto,\n    .mx#{$infix}-auto {\n      margin-right: auto !important;\n    }\n    .mb#{$infix}-auto,\n    .my#{$infix}-auto {\n      margin-bottom: auto !important;\n    }\n    .ml#{$infix}-auto,\n    .mx#{$infix}-auto {\n      margin-left: auto !important;\n    }\n  }\n}\n","// stylelint-disable declaration-no-important\n\n//\n// Text\n//\n\n.text-monospace { font-family: $font-family-monospace; }\n\n// Alignment\n\n.text-justify  { text-align: justify !important; }\n.text-nowrap   { white-space: nowrap !important; }\n.text-truncate { @include text-truncate; }\n\n// Responsive alignment\n\n@each $breakpoint in map-keys($grid-breakpoints) {\n  @include media-breakpoint-up($breakpoint) {\n    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);\n\n    .text#{$infix}-left   { text-align: left !important; }\n    .text#{$infix}-right  { text-align: right !important; }\n    .text#{$infix}-center { text-align: center !important; }\n  }\n}\n\n// Transformation\n\n.text-lowercase  { text-transform: lowercase !important; }\n.text-uppercase  { text-transform: uppercase !important; }\n.text-capitalize { text-transform: capitalize !important; }\n\n// Weight and italics\n\n.font-weight-light  { font-weight: $font-weight-light !important; }\n.font-weight-normal { font-weight: $font-weight-normal !important; }\n.font-weight-bold   { font-weight: $font-weight-bold !important; }\n.font-italic        { font-style: italic !important; }\n\n// Contextual colors\n\n.text-white { color: $white !important; }\n\n@each $color, $value in $theme-colors {\n  @include text-emphasis-variant(\".text-#{$color}\", $value);\n}\n\n.text-body { color: $body-color !important; }\n.text-muted { color: $text-muted !important; }\n\n.text-black-50 { color: rgba($black, .5) !important; }\n.text-white-50 { color: rgba($white, .5) !important; }\n\n// Misc\n\n.text-hide {\n  @include text-hide();\n}\n","// Text truncate\n// Requires inline-block or block for proper styling\n\n@mixin text-truncate() {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n}\n","// stylelint-disable declaration-no-important\n\n// Typography\n\n@mixin text-emphasis-variant($parent, $color) {\n  #{$parent} {\n    color: $color !important;\n  }\n  a#{$parent} {\n    @include hover-focus {\n      color: darken($color, 10%) !important;\n    }\n  }\n}\n","// CSS image replacement\n@mixin text-hide() {\n  // stylelint-disable-next-line font-family-no-missing-generic-family-keyword\n  font: 0/0 a;\n  color: transparent;\n  text-shadow: none;\n  background-color: transparent;\n  border: 0;\n\n}\n","//\n// Visibility utilities\n//\n\n.visible {\n  @include invisible(visible);\n}\n\n.invisible {\n  @include invisible(hidden);\n}\n","// stylelint-disable declaration-no-important\n\n// Visibility\n\n@mixin invisible($visibility) {\n  visibility: $visibility !important;\n}\n","// stylelint-disable declaration-no-important, selector-no-qualifying-type\n\n// Source: https://github.com/h5bp/html5-boilerplate/blob/master/src/css/main.css\n\n// ==========================================================================\n// Print styles.\n// Inlined to avoid the additional HTTP request:\n// https://www.phpied.com/delay-loading-your-print-css/\n// ==========================================================================\n\n@if $enable-print-styles {\n  @media print {\n    *,\n    *::before,\n    *::after {\n      // Bootstrap specific; comment out `color` and `background`\n      //color: $black !important; // Black prints faster\n      text-shadow: none !important;\n      //background: transparent !important;\n      box-shadow: none !important;\n    }\n\n    a {\n      &:not(.btn) {\n        text-decoration: underline;\n      }\n    }\n\n    // Bootstrap specific; comment the following selector out\n    //a[href]::after {\n    //  content: \" (\" attr(href) \")\";\n    //}\n\n    abbr[title]::after {\n      content: \" (\" attr(title) \")\";\n    }\n\n    // Bootstrap specific; comment the following selector out\n    //\n    // Don't show links that are fragment identifiers,\n    // or use the `javascript:` pseudo protocol\n    //\n\n    //a[href^=\"#\"]::after,\n    //a[href^=\"javascript:\"]::after {\n    // content: \"\";\n    //}\n\n    pre {\n      white-space: pre-wrap !important;\n    }\n    pre,\n    blockquote {\n      border: $border-width solid $gray-500;   // Bootstrap custom code; using `$border-width` instead of 1px\n      page-break-inside: avoid;\n    }\n\n    //\n    // Printing Tables:\n    // http://css-discuss.incutio.com/wiki/Printing_Tables\n    //\n\n    thead {\n      display: table-header-group;\n    }\n\n    tr,\n    img {\n      page-break-inside: avoid;\n    }\n\n    p,\n    h2,\n    h3 {\n      orphans: 3;\n      widows: 3;\n    }\n\n    h2,\n    h3 {\n      page-break-after: avoid;\n    }\n\n    // Bootstrap specific changes start\n\n    // Specify a size and min-width to make printing closer across browsers.\n    // We don't set margin here because it breaks `size` in Chrome. We also\n    // don't use `!important` on `size` as it breaks in Chrome.\n    @page {\n      size: $print-page-size;\n    }\n    body {\n      min-width: $print-body-min-width !important;\n    }\n    .container {\n      min-width: $print-body-min-width !important;\n    }\n\n    // Bootstrap components\n    .navbar {\n      display: none;\n    }\n    .badge {\n      border: $border-width solid $black;\n    }\n\n    .table {\n      border-collapse: collapse !important;\n\n      td,\n      th {\n        background-color: $white !important;\n      }\n    }\n    .table-bordered {\n      th,\n      td {\n        border: 1px solid $gray-300 !important;\n      }\n    }\n\n    // Bootstrap specific changes end\n  }\n}\n","/*******************\nPadding margin property \n*******************/\n\n$vertical-property-map: ( m-t-: margin-top, m-b-: margin-bottom, m-r-: margin-right, m-l-: margin-left, p-t-: padding-top, p-b-: padding-bottom, p-r-: padding-right, p-l-: padding-left, p-: padding, m-:margin);\n$sizes-list: 5 10 15 20 25 30 40;\n@each $size in $sizes-list {\n    $val: $size + 0px;\n    @each $keyword,\n    $property in $vertical-property-map {\n        html body .#{$keyword}#{$size} {\n            #{$property}: $val;\n        }\n    }\n}\n\n\n\n/** * Zero */\n\n$zero-property-map: ( m-0: margin, m-t: margin-top, m-r: margin-right, m-b: margin-bottom, m-l: margin-left, p-0: padding, p-t: padding-top, p-r: padding-right, p-b: padding-bottom, p-l: padding-left);\n@each $keyword,\n$property in $zero-property-map {\n    html body .#{$keyword}-0 {\n        #{$property}: 0;\n    }\n}","// Theme colors\n#main-wrapper {\n  &[data-layout='vertical'],\n  &[data-layout='horizontal'] {\n    /*This is for the logo bg*/\n    .topbar .top-navbar .navbar-header {\n      &[data-logobg='skin1'] {\n        background: $skin1;\n      }\n      &[data-logobg='skin2'] {\n        background: $skin2;\n      }\n      &[data-logobg='skin3'] {\n        background: $skin3;\n      }\n      &[data-logobg='skin4'] {\n        background: $skin4;\n      }\n      &[data-logobg='skin5'] {\n        background: $skin5;\n      }\n      &[data-logobg='skin6'] {\n        background: $skin6;\n        .navbar-brand {\n          .dark-logo {\n            display: inline;\n          }\n          .light-logo {\n            display: none;\n          }\n        }\n        .nav-toggler,\n        .topbartoggler {\n          color: $body-color;\n        }\n      }\n    }\n    .topbar .navbar-collapse,\n    .topbar {\n      &[data-navbarbg='skin1'] {\n        background: $skin1;\n      }\n      &[data-navbarbg='skin2'] {\n        background: $skin2;\n      }\n      &[data-navbarbg='skin3'] {\n        background: $skin3;\n      }\n      &[data-navbarbg='skin4'] {\n        background: $skin4;\n      }\n      &[data-navbarbg='skin5'] {\n        background: $skin5;\n      }\n      &[data-navbarbg='skin6'] {\n        background: $skin6;\n      }\n    }\n    /*This is for the sidebar bg*/\n    .left-sidebar {\n      &[data-sidebarbg='skin1'],\n      &[data-sidebarbg='skin1'] ul {\n        background: $skin1;\n        .create-btn {\n          background: $cyan;\n        }\n      }\n      &[data-sidebarbg='skin2'],\n      &[data-sidebarbg='skin2'] ul {\n        background: $skin2;\n      }\n      &[data-sidebarbg='skin3'],\n      &[data-sidebarbg='skin3'] ul {\n        background: $skin3;\n      }\n      &[data-sidebarbg='skin4'],\n      &[data-sidebarbg='skin4'] ul {\n        background: $skin4;\n      }\n      &[data-sidebarbg='skin5'],\n      &[data-sidebarbg='skin5'] ul {\n        background: $skin5;\n        .create-btn {\n          background: $cyan;\n        }\n      }\n      &[data-sidebarbg='skin6'] {\n        background: $skin6;\n        .sidebar-nav {\n          ul {\n            background: $skin6;\n            .sidebar-item {\n              .sidebar-link {\n                color: $sidebar-text-dark;\n                i {\n                  color: $sidebar-icons-dark;\n                }\n              }\n            }\n            .nav-small-cap {\n              color: $sidebar-text-dark;\n              opacity: 0.7;\n            }\n          }\n          .has-arrow {\n            &::after {\n              border-color: $sidebar-text-dark;\n            }\n          }\n          .user-content,\n          .user-name,\n          .user-email {\n            color: $theme-dark;\n          }\n          > ul > li.sidebar-item.selected {\n            > a.sidebar-link,\n            > a.sidebar-link > i {\n              color: $white;\n            }\n            > a.sidebar-link.has-arrow {\n              &::after {\n                border-color: $white;\n              }\n            }\n          }\n        }\n        .create-btn {\n          background: $cyan;\n        }\n      }\n      &[data-sidebarbg='skin1'],\n      &[data-sidebarbg='skin2'],\n      &[data-sidebarbg='skin3'],\n      &[data-sidebarbg='skin4'] {\n        .sidebar-nav {\n          > ul > li.selected > a {\n            background: $dark;\n          }\n        }\n      }\n    }\n  }\n}\n","// App\n\n/*============================================================== \n For all pages \n ============================================================== */\n\n#main-wrapper {\n  width: 100%;\n  overflow: hidden;\n}\n\n.page-wrapper {\n  background: $body-bg;\n  position: relative;\n  transition: 0.2s ease-in;\n}\n\n.page-wrapper > .container-fluid {\n  padding: 20px;\n  min-height: calc(100vh - 180px);\n}\n\n/*******************\n Footer\n*******************/\n\n.footer {\n  padding: 15px 20px;\n}\n\n/*******************\n Stickey inner-left-part\n*******************/\n\n$left-part-width: 260px;\n.left-part {\n  height: calc(100% - 231px);\n  width: $left-part-width;\n  position: absolute;\n  border: 1px solid $border-color;\n  .show-left-part {\n    position: absolute;\n    top: 25%;\n    right: -41px;\n  }\n}\n\n.right-part {\n  width: calc(100% - 260px);\n  min-height: calc(100vh - 64px);\n  overflow: auto;\n  margin-left: $left-part-width;\n  .dropdown-toggle::after {\n    display: none;\n  }\n}\n\n.reverse-mode {\n  .left-part {\n    right: 0;\n    border-left: 1px solid $border-color;\n  }\n  .show-left-part {\n    right: auto;\n    left: -41px;\n  }\n  .right-part {\n    margin-left: 0px;\n    margin-right: $left-part-width;\n  }\n}\n\n/*******************\n Vertical layout\n*******************/\n\n#main-wrapper[data-layout='vertical'] {\n  /*Vertical*/\n  .topbar .top-navbar .navbar-header {\n    //width: $sidebar-width-full;\n    transition: 0.2s ease-in;\n  }\n\n  /*sidebar type*/\n  &[data-sidebartype='full'] {\n    .page-wrapper {\n      margin-left: $sidebar-width-full;\n    }\n  }\n  &[data-sidebartype='iconbar'] {\n    .page-wrapper {\n      margin-left: $sidebar-width-iconbar;\n    }\n    .left-sidebar,\n    .topbar .top-navbar .navbar-header,\n    .sidebar-nav ul .sidebar-item {\n      width: $sidebar-width-iconbar;\n    }\n    .sidebar-nav {\n      ul .sidebar-item {\n        .sidebar-link {\n          display: block;\n          text-align: center;\n          i {\n            display: block;\n            width: auto;\n          }\n        }\n      }\n    }\n  }\n  &[data-sidebartype='overlay'] {\n    .topbar {\n      box-shadow: $shadow;\n    }\n    .topbar .top-navbar .navbar-header {\n      width: $sidebar-width-full;\n    }\n    .left-sidebar {\n      left: -$sidebar-width-full;\n    }\n    .page-wrapper {\n      margin-left: 0px;\n    }\n    &.show-sidebar {\n      .left-sidebar {\n        left: 0;\n      }\n    }\n  }\n  /*Sidebar position*/\n  &[data-sidebar-position='fixed'] {\n    .topbar .top-navbar .navbar-header {\n      position: fixed;\n      z-index: 10;\n    }\n    .left-sidebar {\n      position: fixed;\n    }\n  }\n  /*Header position*/\n  &[data-header-position='fixed'] {\n    .topbar {\n      position: fixed;\n      width: 100%;\n    }\n    .page-wrapper {\n      padding-top: $topbar-height;\n    }\n  }\n  /*Boxed Layout*/\n  &[data-boxed-layout='boxed'] {\n    max-width: $boxed-width;\n    margin: 0 auto;\n    position: relative;\n    box-shadow: 1px 0 80px rgba(0, 0, 0, 0.2);\n  }\n  &[data-boxed-layout='boxed'][data-header-position='fixed'] {\n    .topbar {\n      max-width: $boxed-width;\n    }\n  }\n}\n\n/*******************\n Vertical responsive layout\n*******************/\n\n@include media-breakpoint-up(md) {\n  #main-wrapper[data-layout='vertical'] {\n    /*Vertical*/\n    &[data-sidebartype='full'] {\n      .topbar .top-navbar .navbar-header {\n        width: $sidebar-width-full;\n      }\n    }\n    /*Sidebar position*/\n    &[data-sidebar-position='fixed'][data-sidebartype='full'],\n    &[data-sidebar-position='fixed'][data-sidebartype='overlay'] {\n      .topbar .top-navbar .navbar-collapse {\n        margin-left: $sidebar-width-full;\n      }\n    }\n    &[data-sidebar-position='fixed'][data-sidebartype='mini-sidebar'] {\n      .topbar .top-navbar .navbar-collapse {\n        margin-left: $sidebar-width-mini;\n      }\n    }\n    &[data-sidebar-position='fixed'][data-sidebartype='iconbar'] {\n      .topbar .top-navbar .navbar-collapse {\n        margin-left: $sidebar-width-iconbar;\n      }\n    }\n    /*Vertical with mini-sidebar*/\n    &[data-sidebartype='mini-sidebar'] {\n      .topbar .top-navbar .navbar-header {\n        width: $sidebar-width-mini;\n        .logo-text {\n          display: none;\n        }\n        &.expand-logo {\n          width: $sidebar-width-full;\n          .logo-text {\n            display: block;\n          }\n        }\n      }\n    }\n    /*Fixed sidebar with minisidebar*/\n    &[data-sidebar-position='fixed'][data-sidebartype='mini-sidebar'] {\n      .topbar .top-navbar .navbar-collapse {\n        margin-left: $sidebar-width-mini;\n      }\n    }\n    &[data-sidebartype='mini-sidebar'] {\n      .page-wrapper {\n        margin-left: $sidebar-width-mini;\n      }\n      .sidebar-nav {\n        .hide-menu,\n        .has-arrow:after {\n          display: none;\n        }\n        .nav-small-cap {\n          justify-content: center;\n        }\n      }\n      .left-sidebar {\n        width: $sidebar-width-mini;\n        &:hover {\n          width: $sidebar-width-full;\n          .sidebar-nav {\n            .hide-menu,\n            .has-arrow:after {\n              display: block;\n            }\n            .nav-small-cap {\n              justify-content: flex-start;\n            }\n          }\n        }\n        .first-level {\n          display: none;\n        }\n        &:hover {\n          .first-level.in {\n            display: block;\n          }\n        }\n      }\n    }\n  }\n}\n\n@media (max-width: 766px) {\n  #main-wrapper {\n    &[data-sidebartype='mini-sidebar'] {\n      .left-sidebar {\n        left: -$sidebar-width-full;\n      }\n    }\n    &.show-sidebar {\n      .left-sidebar {\n        left: 0;\n      }\n    }\n    &[data-layout='vertical'][data-sidebar-position='fixed'] {\n      .topbar .top-navbar {\n        .navbar-collapse {\n          position: relative;\n          top: $topbar-height;\n        }\n      }\n    }\n    /*Header position*/\n    &[data-layout='vertical'][data-header-position='fixed'][data-sidebar-position='fixed'] {\n      .topbar .top-navbar {\n        .navbar-collapse {\n          position: relative;\n          top: $topbar-height;\n        }\n      }\n    }\n  }\n  .left-part {\n    left: -$left-part-width;\n    background: $body-bg;\n    z-index: 1;\n    transition: 0.1s ease-in;\n    &.show-panel {\n      left: 0px;\n    }\n  }\n  .reverse-mode .left-part {\n    right: -$left-part-width;\n    left: auto;\n    &.show-panel {\n      right: 0px;\n    }\n  }\n  .right-part {\n    width: 100%;\n    margin-left: 0px;\n  }\n}\n","// scafholding\n* {\n  outline: none;\n}\n\nbody {\n  margin: 0;\n  overflow-x: hidden;\n  color: $body-color;\n  background: $main-body-bg;\n  font-family: $font-family-sans-serif !important;\n}\n\nhtml {\n  position: relative;\n  min-height: 100%;\n  font-family: $font-family-sans-serif !important;\n}\n\na {\n  color: $themecolor;\n}\n\na:hover,\na:focus {\n  text-decoration: none;\n}\n\na.link {\n  color: $body-color;\n  &:hover,\n  &:focus {\n    color: $themecolor;\n  }\n}\n\n.box {\n  border-radius: $border-radius;\n  padding: 10px;\n}\n\n.no-wrap {\n  td,\n  th {\n    white-space: nowrap;\n  }\n}\n\n/*******************\nBlockquote\n*******************/\n\nhtml body blockquote {\n  border-left: 5px solid $primary;\n  border: 1px solid $border-color;\n  padding: 15px;\n}\n\n.clear {\n  clear: both;\n}\n\nol li {\n  margin: 5px 0;\n}\n\n/*******************\nThumb size\n*******************/\n\n.thumb-sm {\n  height: 32px;\n  width: 32px;\n}\n\n.thumb-md {\n  height: 48px;\n  width: 48px;\n}\n\n.thumb-lg {\n  height: 88px;\n  width: 88px;\n}\n\n.hide {\n  display: none;\n}\n\n.radius {\n  border-radius: $border-radius;\n}\n\n/*******************\nOpacity\n*******************/\n\n.op-5 {\n  opacity: 0.5;\n}\n\n.op-3 {\n  opacity: 0.3;\n}\n\n/*******************\nfont weight\n*******************/\n\nhtml body {\n  .font-bold {\n    font-weight: $font-weight-bold;\n  }\n  .font-normal {\n    font-weight: $font-weight-normal;\n  }\n  .font-light {\n    font-weight: $font-weight-light;\n  }\n  .font-medium {\n    font-weight: $font-weight-medium;\n  }\n  .font-16 {\n    font-size: 16px;\n  }\n  .font-12 {\n    font-size: 12px;\n  }\n  .font-14 {\n    font-size: 14px;\n  }\n  .font-10 {\n    font-size: 10px;\n  }\n  .font-18 {\n    font-size: 18px;\n  }\n  .font-20 {\n    font-size: 20px;\n  }\n  .font-22 {\n    font-size: 22px;\n  }\n  .font-24 {\n    font-size: 24px;\n  }\n  .display-5 {\n    font-size: 3rem;\n  }\n  .display-6 {\n    font-size: 2.5rem;\n  }\n  .display-7 {\n    font-size: 2rem;\n  }\n}\n\n/*******************\nBackground colors\n*******************/\n\nhtml body {\n  .bg-megna {\n    background-color: $cyan;\n  }\n  .bg-theme {\n    background-color: $themecolor;\n  }\n  .bg-inverse {\n    background-color: $gray-900;\n  }\n  .bg-purple {\n    background-color: $purple;\n  }\n  .bg-light {\n    background-color: $gray-200;\n  }\n  .bg-white {\n    background-color: $white;\n  }\n}\n\n/*******************\nRounds\n*******************/\n\n.round {\n  line-height: 48px;\n  color: $white;\n  width: 50px;\n  height: 50px;\n  display: inline-block;\n  font-weight: 400;\n  text-align: center;\n  border-radius: 100%;\n  background: $blue;\n  line-height: 52px;\n  img {\n    border-radius: 100%;\n  }\n  &.round-info {\n    background: $blue;\n  }\n\n  &.round-warning {\n    background: $warning;\n  }\n\n  &.round-danger {\n    background: $danger;\n  }\n\n  &.round-success {\n    background: $success;\n  }\n\n  &.round-primary {\n    background: $primary;\n  }\n}\n\n.round-lg {\n  line-height: 65px;\n  width: 60px;\n  height: 60px;\n  font-size: 30px;\n}\n\n/*******************\nLabels\n*******************/\n\n.label-rounded {\n  border-radius: 60px;\n}\n\n/*******************\n Badge\n******************/\n\n.badge {\n  font-weight: $font-weight-light;\n  line-height: normal;\n  &.badge-pill {\n    padding: 0.2em 0.6em;\n  }\n}\n\n.badge-xs {\n  font-size: 9px;\n}\n\n.badge-xs,\n.badge-sm {\n  -webkit-transform: translate(0, -2px);\n  -ms-transform: translate(0, -2px);\n  -o-transform: translate(0, -2px);\n  transform: translate(0, -2px);\n}\n\n/*******************\nList-style-none\n******************/\n\nul.list-style-none {\n  margin: 0px;\n  padding: 0px;\n  li {\n    list-style: none;\n    a {\n      color: $body-color;\n      padding: 8px 0px;\n      display: block;\n      text-decoration: none;\n      &:hover {\n        color: $themecolor;\n      }\n    }\n  }\n}\n\n/*******************\nCard\n******************/\n\n.card {\n  margin-bottom: 20px;\n  border: 1px solid rgba(0, 0, 0, 0.1);\n  .card-subtitle {\n    font-weight: $font-weight-light;\n    margin-bottom: 10px;\n    color: $text-muted;\n  }\n  .card-title {\n    position: relative;\n    font-weight: $font-weight-medium;\n    margin-bottom: 10px;\n  }\n  .card-actions {\n    float: $rgt;\n    a {\n      padding: 0 5px;\n      cursor: pointer;\n    }\n  }\n  .card-header .card-title {\n    margin-bottom: 0px;\n  }\n  &.no-card-border {\n    border: 0px;\n  }\n}\n\n.card-alt {\n  margin: 0 -20px;\n  background: $card-alt;\n}\n\n.card-group {\n  margin-bottom: 20px;\n  .card {\n    border-right: 1px solid rgba(0, 0, 0, 0.1);\n  }\n}\n\n.card-fullscreen {\n  position: fixed;\n  top: 0px;\n  left: 0px;\n  width: 100%;\n  height: 100%;\n  z-index: 9999;\n  overflow: auto;\n}\n\n.oh {\n  overflow: hidden;\n}\n.card-hover {\n  -webkit-transition: all 0.25s ease;\n  -o-transition: all 0.25s ease;\n  -moz-transition: all 0.25s ease;\n  transition: all 0.25s ease;\n  &:hover {\n    webkit-transform: translateY(-4px) scale(1.01);\n    -moz-transform: translateY(-4px) scale(1.01);\n    -ms-transform: translateY(-4px) scale(1.01);\n    -o-transform: translateY(-4px) scale(1.01);\n    transform: translateY(-4px) scale(1.01);\n    -webkit-box-shadow: 0 14px 24px rgba(62, 57, 107, 0.1);\n    box-shadow: 0 14px 24px rgba(62, 57, 107, 0.1);\n  }\n}\n.draggable-cards .card-header {\n  cursor: move;\n}\n.card-moved .card {\n  background: $info;\n  color: $white;\n}\n/*******************\nLabels\n*******************/\n\n.label {\n  padding: 3px 10px;\n  line-height: 13px;\n  color: $white;\n  font-weight: 400;\n  border-radius: $border-radius;\n  font-size: 75%;\n}\n\n.label-rounded {\n  border-radius: 60px;\n}\n\n.label-custom {\n  background-color: $cyan;\n}\n\n.label-success {\n  background-color: $success;\n}\n\n.label-info {\n  background-color: $blue;\n}\n\n.label-warning {\n  background-color: $warning;\n}\n\n.label-danger {\n  background-color: $danger;\n}\n\n.label-megna {\n  background-color: $cyan;\n}\n\n.label-primary {\n  background-color: $primary;\n}\n\n.label-purple {\n  background-color: $purple;\n}\n\n.label-red {\n  background-color: $red;\n}\n\n.label-inverse {\n  background-color: $gray-800;\n}\n\n.label-default {\n  background-color: $gray-100;\n}\n","\n\n.preloader{\n  position: absolute;\n  margin: 0 auto;\n  width: 100%;\n  height: 100%;\n}\n.spinner {\n  width: 40px;\n  height: 40px;\n  top:35%; \n  position: relative;\n  margin: 100px auto;\n}\n\n.double-bounce1, .double-bounce2 {\n  width: 100%;\n  height: 100%;\n  border-radius: 50%;\n  background-color: #1976d2;\n  opacity: 0.6;\n  position: absolute;\n  top: 0;\n  left: 0;\n  \n  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;\n  animation: sk-bounce 2.0s infinite ease-in-out;\n}\n\n.double-bounce2 {\n  -webkit-animation-delay: -1.0s;\n  animation-delay: -1.0s;\n}\n\n@-webkit-keyframes sk-bounce {\n  0%, 100% { -webkit-transform: scale(0.0) }\n  50% { -webkit-transform: scale(1.0) }\n}\n\n@keyframes sk-bounce {\n  0%, 100% { \n    transform: scale(0.0);\n    -webkit-transform: scale(0.0);\n  } 50% { \n    transform: scale(1.0);\n    -webkit-transform: scale(1.0);\n  }\n}","/*******************\nNotify\n*******************/\n\n.notify {\n    position: relative;\n    top: -35px;\n    right: -8px;\n    .heartbit {\n        position: absolute;\n        top: -20px;\n        right: -4px;\n        height: 25px;\n        width: 25px;\n        z-index: 10;\n        border: 5px solid $danger;\n        border-radius: 70px;\n        -moz-animation: heartbit 1s ease-out;\n        -moz-animation-iteration-count: infinite;\n        -o-animation: heartbit 1s ease-out;\n        -o-animation-iteration-count: infinite;\n        -webkit-animation: heartbit 1s ease-out;\n        -webkit-animation-iteration-count: infinite;\n        animation-iteration-count: infinite;\n    }\n    .point {\n        width: 6px;\n        height: 6px;\n        -webkit-border-radius: 30px;\n        -moz-border-radius: 30px;\n        border-radius: 30px;\n        background-color: $danger;\n        position: absolute;\n        right: 6px;\n        top: -10px;\n    }\n}\n\n@-moz-keyframes heartbit {\n    0% {\n        -moz-transform: scale(0);\n        opacity: 0.0;\n    }\n    25% {\n        -moz-transform: scale(0.1);\n        opacity: 0.1;\n    }\n    50% {\n        -moz-transform: scale(0.5);\n        opacity: 0.3;\n    }\n    75% {\n        -moz-transform: scale(0.8);\n        opacity: 0.5;\n    }\n    100% {\n        -moz-transform: scale(1);\n        opacity: 0.0;\n    }\n}\n\n@-webkit-keyframes heartbit {\n    0% {\n        -webkit-transform: scale(0);\n        opacity: 0.0;\n    }\n    25% {\n        -webkit-transform: scale(0.1);\n        opacity: 0.1;\n    }\n    50% {\n        -webkit-transform: scale(0.5);\n        opacity: 0.3;\n    }\n    75% {\n        -webkit-transform: scale(0.8);\n        opacity: 0.5;\n    }\n    100% {\n        -webkit-transform: scale(1);\n        opacity: 0.0;\n    }\n}","// header part\n@import \"notify\";\n\n/*******************\n General Topbar\n*******************/\n\n.topbar {\n  position: relative;\n  z-index: 50;\n  transition: $transitions;\n  //box-shadow: 1px 0px 7px rgba(0, 0, 0, 0.05);\n  background: $topbar;\n  height: $topbar-height;\n  top: 0;\n  .navbar-collapse {\n    padding: 0px 10px 0 0;\n  }\n  .top-navbar {\n    min-height: $topbar-height;\n    padding: 0px;\n    .dropdown-toggle::after {\n      display: none;\n    }\n    .navbar-header {\n      line-height: $topbar-navlink-height;\n      border-right: 1px solid rgba(0, 0, 0, 0.1);\n\n      .navbar-brand {\n        display: flex;\n        align-items: center;\n        margin: 0px;\n        text-align: center;\n        padding: $topbar-navbrand-padding;\n        .dark-logo {\n          display: none;\n        }\n        .logo-icon {\n          margin-right: 5px;\n        }\n      }\n    }\n    .navbar-nav > .nav-item > .nav-link {\n      padding: $topbar-navlink-padding;\n      font-size: $topbar-navlink-font-size;\n      line-height: $topbar-navlink-height;\n      height: $topbar-navlink-height;\n    }\n    .navbar-nav > .nav-item {\n      transition: 0.2s ease-in;\n    }\n  }\n  .user-dd,\n  .mailbox {\n    min-width: 280px;\n  }\n  .nav-toggler,\n  .topbartoggler {\n    color: $theme-light;\n    padding: 0 15px;\n  }\n}\n\n.search-box {\n  .app-search {\n    z-index: 110;\n    width: 100%;\n    top: -1px;\n    box-shadow: $shadow;\n    display: none;\n    left: 0px;\n    &.show-search {\n      display: block;\n    }\n    .form-control {\n      padding: 23px 40px 20px 23px;\n      @extend %square;\n      transition: $transitions;\n      &:focus {\n        border-color: transparent;\n      }\n    }\n    .srh-btn {\n      position: absolute;\n      top: 23px;\n      cursor: pointer;\n      right: 20px;\n    }\n  }\n}\n\n/*******************\nMegamenu\n******************/\n.topbar {\n  .mega-dropdown {\n    position: static;\n    .dropdown-menu {\n      padding: 30px;\n      @extend %square;\n      width: 100%;\n      max-height: 480px;\n      overflow: auto;\n      transform: translate(0px, 58px) !important;\n    }\n  }\n}\n/*******************\nDesigned dropdown\n******************/\n.topbar {\n  .dropdown-menu {\n    padding-top: 0px;\n    border: 0px;\n    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.1);\n    .with-arrow {\n      position: absolute;\n      height: 10px;\n      overflow: hidden;\n      width: 40px;\n      top: -10px;\n      > span {\n        @extend %rotate45;\n        background-color: $white;\n        width: 15px;\n        height: 15px;\n        top: 3px;\n        left: 15px;\n        position: absolute;\n        content: \"\";\n        -moz-border-radius: 6px 0 0 0;\n        border-radius: 6px 0 0 0;\n        /*box-shadow:-3px -3px 8px rgba(0, 0, 0, 0.1);    */\n      }\n    }\n    &.dropdown-menu-right {\n      .with-arrow {\n        right: 0px;\n        > span {\n          right: 20px;\n          left: auto;\n        }\n      }\n    }\n  }\n}\n/*******************\nTopbar Responsive\n******************/\n\n@media (max-width: 767px) {\n  .topbar .top-navbar {\n    .navbar-collapse.show,\n    .navbar-collapse.collapsing {\n      display: block;\n      width: 100%;\n      border-top: 1px solid $border-color;\n    }\n    .navbar-nav {\n      flex-direction: row;\n      .dropdown {\n        position: static;\n      }\n    }\n    .navbar-nav > .nav-item > .nav-link {\n      padding: 0 10px;\n    }\n    .navbar-header {\n      display: flex;\n      align-items: center;\n      width: 100%;\n      justify-content: space-between;\n    }\n    .dropdown-menu {\n      position: absolute;\n      width: 100%;\n      left: 0 !important;\n      .with-arrow {\n        display: none;\n      }\n    }\n  }\n}\n","/*\nTemplate Name: Admin Template\nAuthor: Wrappixel\n\nFile: scss\n*/\n\n\n\n/*******************\nTable Cell\n*******************/\n\n.table-box {\n    display: table;\n    width: 100%;\n}\n\n.cell {\n    display: table-cell;\n    vertical-align: middle;\n}\n\n.table thead th,\n.table th {\n    font-weight: 500;\n}\n\n.nowrap {\n    white-space: nowrap;\n}\n\n.lite-padding td {\n    padding: 5px;\n}\n\n.v-middle {\n    td,\n    th {\n        vertical-align: middle;\n    }\n}\n\n/*******************\nTable td vertical middle\n*******************/\n\n.vm.table td,\n.vm.table th {\n    vertical-align: middle;\n}\n\n.no-th-brd.table th {\n    border: 0px;\n}\n\n.table.no-border {\n    tbody {\n        td {\n            border: 0px;\n        }\n    }\n}\n\n\n\n/*******************\nTable-Jsgrid table\n******************/\n\n.jsgrid-pager-page a,\n.jsgrid-pager-current-page,\n.jsgrid-pager-nav-button a {\n    -webkit-box-sizing: border-box;\n    box-sizing: border-box;\n    display: inline-block;\n    min-width: 1.5em;\n    padding: 0.5em 1em;\n    text-align: center;\n    text-decoration: none;\n    cursor: pointer;\n    color: #67757c;\n    border: 1px solid #ddd;\n}\n\n.jsgrid-pager-page a:hover,\n.jsgrid-pager-nav-button a:hover {\n    background-color: $themecolor;\n    color: $white;\n}\n\n.jsgrid-pager-current-page {\n    background-color: $themecolor;\n    color: $white;\n}\n\n.jsgrid-pager-page,\n.jsgrid-pager-nav-button {\n    padding: 0;\n}\n\n.jsgrid-pager-page.jsgrid-pager-current-page {\n    padding: 0.5em 1em !important;\n}","/*******************\n Breadcrumb and page title\n*******************/\n\n.page-breadcrumb {\n    padding: 20px 20px 0 20px;  \n    .page-title{\n        margin-bottom: 0px;\n    }\n    .breadcrumb {\n        padding: 0px;\n        margin: 0px;\n        background: transparent;\n        font-size: 12px;\n        .breadcrumb-item+.breadcrumb-item::before {\n            content: \"\\e649\";\n            font-family: themify;\n            color: $gray-400;\n            font-size: 11px;\n        }\n    }\n}\n","// Sidebar\n$sidebar-link-padding: 12px 15px;\n$sidebar-icon-size: 20px;\n$sidebar-first-level-padding: 0 0 10px 0;\n$sidebar-first-level-link-padding: 10px 15px;\n$sidebar-first-level-icon-size: 14px;\n\n/*******************\nMain sidebar\n******************/\n\n.left-sidebar {\n  position: absolute;\n  width: $sidebar-width-full;\n  height: 100%;\n  top: 0px;\n  border-right: 1px solid rgba(0, 0, 0, 0.1);\n  z-index: 10;\n  padding-top: 64px;\n  background: $sidebar;\n  //box-shadow: $shadow;\n  transition: 0.2s ease-in;\n}\n\n/*******************\nsidebar navigation\n******************/\n.scroll-sidebar {\n  height: calc(100% - 20px);\n  position: relative;\n}\n\n.sidebar-nav {\n  ul {\n    .sidebar-item {\n      .sidebar-link {\n        color: $sidebar-text;\n        padding: $sidebar-link-padding;\n        display: flex;\n        white-space: nowrap;\n        align-items: center;\n        line-height: 25px;\n        opacity: 0.8;\n        i {\n          font-style: normal;\n          width: 35px;\n          line-height: 25px;\n          font-size: $sidebar-icon-size;\n          color: $sidebar-text;\n          display: inline-block;\n          text-align: center;\n        }\n        &.active,\n        &:hover {\n          opacity: 1;\n        }\n      }\n      &.selected > .sidebar-link {\n        /*background: rgba(0, 0, 0, 0.04);*/\n        opacity: 1;\n        color: $sidebar-text-dark;\n        i{\n          color: $sidebar-text-dark;\n        }\n      }\n      /*******************\n      First level\n      ******************/\n      .first-level {\n        padding: $sidebar-first-level-padding;\n\n        .sidebar-item.active .sidebar-link {\n          opacity: 1;\n        }\n        .sidebar-item .sidebar-link {\n          padding: $sidebar-first-level-link-padding;\n          i {\n            font-size: $sidebar-first-level-icon-size;\n            visibility: hidden;\n          }\n        }\n      }\n    }\n    /*******************\n         Small cap\n         ******************/\n    .nav-small-cap {\n      font-size: 12px;\n      padding: $sidebar-link-padding;\n      white-space: nowrap;\n      display: flex;\n      align-items: center;\n      line-height: 30px;\n      margin-top: 10px;\n      color: $sidebar-text;\n      opacity: 0.5;\n      text-transform: uppercase;\n      i {\n        line-height: 30px;\n        margin: 0 5px;\n      }\n    }\n  }\n}\n/*******************\n common m-p removed\n******************/\n.sidebar-nav {\n  ul {\n    margin: 0px;\n    padding: 0px;\n    li {\n      list-style: none;\n    }\n  }\n  > ul > li.selected {\n    padding: 0 0 0 10px;\n    > a {\n      border-radius: 60px 0 0 60px;\n      background: $gray-100;\n      color: $white;\n    }\n  }\n}\n.collapse.in {\n  display: block;\n}\n/*******************\nDown arrow\n******************/\n.sidebar-nav .has-arrow 