inter;\n    display: inline-block;\n    overflow: hidden;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n    -webkit-tap-highlight-color: transparent;\n    vertical-align: middle;\n    z-index: 1;\n    will-change: opacity, transform;\n    -webkit-transition: all 0.1s ease-out;\n    -moz-transition: all 0.1s ease-out;\n    -o-transition: all 0.1s ease-out;\n    -ms-transition: all 0.1s ease-out;\n    transition: all 0.1s ease-out;\n    .waves-ripple {\n        position: absolute;\n        border-radius: 50%;\n        width: 20px;\n        height: 20px;\n        margin-top: -10px;\n        margin-left: -10px;\n        opacity: 0;\n        background: rgba(0, 0, 0, 0.2);\n        -webkit-transition: all 0.7s ease-out;\n        -moz-transition: all 0.7s ease-out;\n        -o-transition: all 0.7s ease-out;\n        -ms-transition: all 0.7s ease-out;\n        transition: all 0.7s ease-out;\n        -webkit-transition-property: -webkit-transform, opacity;\n        -moz-transition-property: -moz-transform, opacity;\n        -o-transition-property: -o-transform, opacity;\n        transition-property: transform, opacity;\n        -webkit-transform: scale(0);\n        -moz-transform: scale(0);\n        -ms-transform: scale(0);\n        -o-transform: scale(0);\n        transform: scale(0);\n        pointer-events: none;\n    }\n}\n\n.waves-effect.waves-light {\n    .waves-ripple {\n        background-color: rgba(255, 255, 255, 0.45);\n    }\n}\n\n.waves-effect.waves-red {\n    .waves-ripple {\n        background-color: rgba(244, 67, 54, 0.7);\n    }\n}\n\n.waves-effect.waves-yellow {\n    .waves-ripple {\n        background-color: rgba(255, 235, 59, 0.7);\n    }\n}\n\n.waves-effect.waves-orange {\n    .waves-ripple {\n        background-color: rgba(255, 152, 0, 0.7);\n    }\n}\n\n.waves-effect.waves-purple {\n    .waves-ripple {\n        background-color: rgba(156, 39, 176, 0.7);\n    }\n}\n\n.waves-effect.waves-green {\n    .waves-ripple {\n        background-color: rgba(76, 175, 80, 0.7);\n    }\n}\n\n.waves-effect.waves-teal {\n    .waves-ripple {\n        background-color: rgba(0, 150, 136, 0.7);\n    }\n}\n\nhtml body .waves-notransition {\n    -webkit-transition: none;\n    -moz-transition: none;\n    -o-transition: none;\n    -ms-transition: none;\n    transition: none;\n}\n\n.waves-circle {\n    -webkit-transform: translateZ(0);\n    -moz-transform: translateZ(0);\n    -ms-transform: translateZ(0);\n    -o-transform: translateZ(0);\n    transform: translateZ(0);\n    text-align: center;\n    width: 2.5em;\n    height: 2.5em;\n    line-height: 2.5em;\n    border-radius: 50%;\n    -webkit-mask-image: none;\n}\n\n.waves-input-wrapper {\n    border-radius: 0.2em;\n    vertical-align: bottom;\n    .waves-button-input {\n        position: relative;\n        top: 0;\n        left: 0;\n        z-index: 1;\n    }\n}\n\n.waves-block {\n    display: block;\n}","//Buttons\n\n.btn-circle {\n    border-radius: 100%;\n    width: 40px;\n    height: 40px;\n    padding: 10px;\n    &.btn-sm {\n        width: 35px;\n        height: 35px;\n        padding: 8px 10px;\n        font-size: 14px;\n    }\n    &.btn-lg {\n        width: 50px;\n        height: 50px;\n        padding: 14px 15px;\n        font-size: 18px;\n        line-height: 23px;\n    }\n    &.btn-xl {\n        width: 70px;\n        height: 70px;\n        padding: 14px 15px;\n        font-size: 24px;\n    }\n\n}\n\n.btn-xs {\n    padding: .25rem .5rem;\n    font-size: 10px;\n}\n\n.btn-rounded {\n    border-radius: 60px;\n    padding: 7px 18px;\n\n    &.btn-lg {\n        padding: .75rem 1.5rem;\n    }\n    &.btn-sm {\n        padding: .25rem .5rem;\n        font-size: 12px;\n    }\n    &.btn-xs {\n        padding: .25rem .5rem;\n        font-size: 10px;\n    }\n    &.btn-md {\n        padding: 12px 35px;\n        font-size: 16px;\n    }\n} \n\n\n.btn-primary, .btn-default, .btn-warning, .btn-outline-primary:hover, .btn-outlin