important;\n    background: $gray-900;\n    color: $white;\n    padding: 5px 10px;\n  }\n}\n\nbody .jqstooltip {\n  border-color: transparent;\n  border-radius: 60px;\n}\n\n// Tooltip for the chartist chart charts\n.chartist-tooltip {\n  position: absolute;\n  display: inline-block;\n  opacity: 0;\n  border-radius: $border-radius;\n  padding: 10px 20px;\n  background: $info;\n  color: $white;\n  ext-align: center;\n  pointer-events: none;\n  z-index: 1;\n  -webkit-transition: opacity 0.2s linear;\n  -moz-transition: opacity 0.2s linear;\n  -o-transition: opacity 0.2s linear;\n  transition: opacity 0.2s linear;\n}\n\n.chartist-tooltip:before {\n  content: \"\";\n  position: absolute;\n  top: 100%;\n  left: 50%;\n  width: 0;\n  height: 0;\n  margin-left: -5px;\n  border: 5px solid transparent;\n  border-top-color: $info;\n}\n\n.chartist-tooltip.tooltip-show {\n  opacity: 1;\n}\n\n.ct-area,\n.ct-line {\n  pointer-events: none;\n}\n\n.ct-series-a .ct-area {\n  fill: $cyan;\n}\n\n.ct-series-b .ct-area {\n  fill: $info;\n}\n\n.ct-series-c .ct-area {\n  fill: $indigo;\n}\n\n.ct-series-c .ct-area {\n  fill: $success;\n}\n\n.ct-series-a .ct-point,\n.ct-series-a .ct-line,\n.ct-series-a .ct-bar,\n.ct-series-a .ct-slice-donut {\n  stroke: $cyan;\n}\n\n.ct-series-b .ct-point,\n.ct-series-b .ct-line,\n.ct-series-b .ct-bar,\n.ct-series-b .ct-slice-donut {\n  stroke: $info;\n}\n\n.ct-series-c .ct-point,\n.ct-series-c .ct-line,\n.ct-series-c .ct-bar,\n.ct-series-c .ct-slice-donut {\n  stroke: $indigo;\n}\n\n.ct-series-d .ct-point,\n.ct-series-d .ct-line,\n.ct-series-d .ct-bar,\n.ct-series-d .ct-slice-donut {\n  stroke: $success;\n}\n\n.c3-chart-arcs-title {\n  fill: $text-muted;\n}\n\n.c3 line,\n.c3 path {\n  stroke: $gray-200;\n}\n.c3 text {\n  fill: $text-muted;\n}\n.c3-chart-arc path {\n  stroke: transparent;\n}\n.popover-item {\n  margin-left: -15px;\n  &:hover {\n    z-index: 10;\n    position: relative;\n  }\n}\n\n.icon-list-demo {\n  .preview {\n    padding: 10px;\n    i {\n 