 0;\n  }\n  .taskboard-header {\n    position: relative;\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    text-transform: uppercase;\n    letter-spacing: 0.02rem;\n    padding: 8px 8px 0;\n  }\n  .taskboard-task {\n    position: relative;\n    display: flex;\n    flex-direction: column;\n    padding: 8px;\n    margin-bottom: 8px;\n    background: $white;\n    &:last-child {\n      margin-bottom: 0;\n    }\n    &:hover {\n      cursor: grab;\n    }\n  }\n  .taskboard-task:after {\n    content: \"\";\n    position: absolute;\n    border: 4px solid transparent;\n    top: 0;\n    border-top-width: 12px;\n    border-bottom-color: transparent;\n    right: 6px;\n  }\n  .taskboard-task.task-status-success:after {\n    border-top-color: $success;\n    border-right-color: $success;\n    border-left-color: $success;\n  }\n  .taskboard-task.task-status-info:after {\n    border-top-color: $info;\n    border-right-color: $info;\n    border-left-color: $info;\n  }\n  .taskboard-task.task-status-warning:after {\n    border-top-color: $warning;\n    border-right-color: $warning;\n    border-left-color: $warning;\n  }\n  .taskboard-task.task-status-danger:after {\n    border-top-color: $danger;\n    border-right-color: $danger;\n    border-left-color: $danger;\n  }\n  .taskboard-cards {\n    padding: 8px;\n    box-sizing: border-box;\n    overflow-x: hidden;\n    overflow-y: auto;\n  }\n  .taskboard-task-title {\n    margin-bottom: 8px;\n  }\n}\n\n.ng2-smart-page-item {\n  border: 1px solid $border-color;\n  .ng2-smart-page-link {\n    border: none;\n  }\n  &.active {\n    border-color: $themecolor;\n    .ng2-smart-page-link {\n      border-color: transparent;\n    }\n  }\n}\n\n.ngx-datatable.material {\n  box-shadow: none;\n  border: 1px solid $border-color;\n}\n\n// File Upload Plugin\n\n.afu-select-btn {\n  margin-bottom: 0;\n}\n\n.afu-valid-file {\n    .progress {\n        width: 500px;\n        height: 13px;\n    }\n}\n\n#attachPin {\n    .label:empty {\n        display: none;\n    }\n}\n\n/*******************\nNgx wizard pages scss\n******************/\n\n.wizard-navbar {\n  padding: 0px;\n  margin: 50px 0 50px 0;\n  display: flex;\n  position: relative;\n  align-items: center;\n  justify-content: center;\n\n  &::before {\n    height: 2px;\n    background: $border-color;\n    content: \"\";\n    position: absolute;\n    width: 75%;\n    margin: 0 auto;\n    left: 0;\n    right: 0;\n    top: 50%;\n    z-index: 1;\n  }\n\n  li {\n    list-style: none;\n    text-align: center;\n\n    .round-tabs {\n      padding: 20px;\n      background: white;\n      position: relative;\n      z-index: 2;\n      border: 1px solid $border-color;\n      border-radius: 100%;\n\n    }\n\n    >a {\n      padding: 0.5rem 0;\n    }\n\n    >a.active .round-tabs {\n      background: $info;\n      border-color: $info;\n      color: $white;\n    }\n  }\n}\n\n#ngx .ng-invalid,\n.ng-touched.ng-invalid {\n  border-color: $danger;\n}\n\n#ngx .ng-valid,\n.ng-touched.ng-valid {\n  border-color: $success;\n}\n\n/*******************\nNotifier pages scss\n******************/\n.notifier__container * {\n  box-sizing: border-box;\n}\n\n.notifier__container-list {\n  margin-top: 0;\n  margin-bottom: 0;\n  padding-left: 0;\n  list-style-type: none;\n}\n\n.notifier__notification {\n  display: block;\n  position: fixed;\n  visibility: hidden;\n  z-index: 10000;\n  will-change: transform;\n  backface-visibility: hidden;\n}\n$notifier-shadow-color: rgba(0, 0, 0, .2) !default;\n\n.notifier__notification--material {\n  border-radius: 3px;\n  box-shadow: 0 1px 3px $notifier-shadow-color;\n  cursor: default; // Default cursor, even when hovering over text\n  padding: {\n    top: 11px;\n    right: 26px;\n    bottom: 10px;\n    left: 26px;\n  }\n\n  .notifier__notification {\n\n    &-message {\n      display: inline-block;\n      margin: { // Reset paragraph default styles\n        top: 0;\n        bottom: 0;\n      }\n      vertical-align: top;\n      line-height: 32px;\n      font-size: 15px;\n    }\n\n    &-button {\n      display: inline-block;\n      transition: opacity .2s ease;\n      opacity: .5;\n      margin: {\n        right: -10px;\n        left: 10px;\n        top:-4px;\n      };\n      outline: none;\n      border: none;\n      background: none;\n      cursor: pointer; // Make it obvious that the \"button\" (or, more honestly, icon) is clickable (#UX)\n      padding: 6px;\n      width: 32px;\n      height: 32px;\n      vertical-align: top;\n\n      &:hover,\n      &:focus {\n        opacity: 1; // Make me \"feel\" the clickability with a transparency change (#UX)\n      }\n\n      &:active {\n        transform: scale(.82); // Make me \"feel\" the click by a push back (#UX)\n        opacity: 1;\n      }\n\n    }\n\n  }\n\n}\n.notifier__notification--default {\n  background-color: $primary;\n  color: $white;\n\n  .notifier__notification-button-icon { // 16x16 fixed size\n    fill: $white;\n  }\n\n}\n\n.notifier__notification--info {\n  background-color: $info;\n  color: $white;\n\n  .notifier__notification-button-icon { // 16x16 fixed size\n    fill: $white;\n  }\n\n}\n.notifier__notification--warning {\n  background-color: $warning;\n  color: $white;\n\n  .notifier__notification-button-icon { // 16x16 fixed size\n    fill: $white;\n  }\n\n}\n.notifier__notification--success {\n  background-color: $success;\n  color: $white;\n\n  .notifier__notification-button-icon { // 16x16 fixed size\n    fill: $white;\n  }\n\n}\n.notifier__notification--error {\n  background-color: $danger;\n  color: $white;\n\n  .notifier__notification-button-icon { // 16x16 fixed size\n    fill: $white;\n  }\n\n}\n\n.card {\n\toverflow: unset;\n}\n","/*******************\nPadding margin property \n*******************/\n\n$vertical-property-map: ( m-t-: margin-top, m-b-: margin-bottom, m-r-: margin-left, m-l-: margin-right, p-t-: padding-top, p-b-: padding-bottom, p-r-: padding-left, p-l-: padding-right, p-: padding, m-:margin);\n$sizes-list